import { Component } from "react";
import React from "react";
import Cipka from "./pictureView";
class Test extends Component{

state={
    pics:[{src:"https://cdni.pornpics.com/1280/1/185/18720073/18720073_006_0274.jpg"},
          {src:"https://cdni.pornpics.com/1280/5/196/60135294/60135294_005_cd54.jpg"},
          {src:"https://s9v7j7a4.ssl.hwcdn.net/galleries/full/4f/6b/31/4f6b31118eb3f1261a39fe1ca2e403cd/5.jpg"}]



}
deletePics=(index)=>{

const copy=Object.assign([],this.state.pics);
alert(index)
copy.splice(index,1);

this.setState({pics:copy})

}


render(){

return(
<div>

{this.state.pics.map((index)=>{
    return <Cipka id={this.state.pics.indexOf(index)} src={index.src} delete={this.deletePics.bind(this,this.state.pics.indexOf(index))}/>
})}


</div>


)


}

}
export default Test;
