import React from 'react'
import  { Component } from "react";
import Calendar from 'react-calendar';
import '../css/Calendar.css'
import HoursForm from './EnterHoursForm';
import { Link } from 'react-router-dom';
export default class JobHours extends Component{

state={data:undefined,
       year:0,
       month:0,
       day:0,
       isWeek:0
     }



    render(){
        return(<div>
            <div style={{display:'flex'}}>
            <div>

           <Calendar     onClickDay={(event)=>{this.setState({year:event.getFullYear(),
                                                          month:event.getMonth(),
                                                          day:event.getDate(),
                                                          data:event.toLocaleDateString(),
                                                          isWeek:event.getDay()===6?1:0
                                                          }  )
                                                                 
        }


             
        } className={['c1','c2']}/>
                </div>
                <div  style={{marginTop:"2%"}}><Link to={{ pathname: "/hoursInfo"}}><p className='formStringFields'>Wyświetl informacje...</p></Link></div>
                </div>
                <div style={{display:'flex',marginLeft:'5%'}}>

               
                <div  id='formContainer'  >
                    <h2 className='formStringFields'>Dodaj nowe nadgodziny...</h2>
                    <div style={{display:'flex'}}>
                    <p className='formStringFields'>Wybierz date...</p><p style={{marginLeft:'2%'}} className='formStringFields'>  {'   '+this.state.data?this.state.data:'Wybierz dzień...'}</p>
                    </div>
                    <HoursForm data={this.state.data} isWeek={this.state.isWeek} month={this.state.month} year={this.state.year} day={this.state.day}  />
                </div>
               
                </div>
        </div>)
    }
}