

import React, { Component } from 'react';

import { getListOfMonths, getHours,delHours } from './apiConnect';
import { sumHours, sumNext ,sumSetki} from './functions';
import Welcome from './hoursComponents/editHours';
import '../css/galeria.css'


export default class HoursInfo extends Component{
  state={
  months:[],
  godziny:[],
  month:'',
  overload:1,
  update:false
  }

constructor(props){
  super(props)

  
   
  this.inputRef = React.createRef();
}
up=()=>{
 
  this.setState({overload:this.state.overload?0:1})
 
}
componentDidUpdate(){
 
}
componentDidMount(){
  getListOfMonths().then((response) => {
    const table = [];
    response.forEach(element => {table.push(element.table_name)});
      this.setState({months:table})
  })
}
 

    render(){

        return(<div id='monthsButtons' style={{display:'grid', width:"50px"}}>
            
             {
                 this.state.months.map((data)=>{return  <button key={data} onClick={()=>{getHours(data).then((value)=>{this.setState({godziny:value,month:data})})}} className='formStringFields2'>{data}</button>
                
                
                
                }
                
                )
               
                                       
             }
             <p className='formStringFields'>Godziny:</p><p className='formStringFields'>{sumHours(this.state.godziny)}</p><br/>
             <p className='formStringFields'>Nadgodziny:</p><p className='formStringFields'>{sumNext(this.state.godziny)}</p><br/>
             <p className='formStringFields'>Setki:</p><p className='formStringFields'>{sumSetki(this.state.godziny)}</p><br/>
           
             <ul style={{display:'grid'}}>
           
            {
          
            this.state.godziny.map((value)=>{
            return  (<div style={{width:"400px"}}>
            <div><Welcome val={value} up={this.state.overload} month={this.state.month}/></div>
            <div style={{display:'flex'}}>
              
              
            <button  onClick={()=>{delHours({data:value.data,month:this.state.month});this.setState({overload:this.state.overload?0:1})}}  className="buttonDelete"  ><span className="text">Delete</span><span className="icon"><svg  width="24" height="24" viewBox="0 0 24 24"><path d="M24 20.188l-8.315-8.209 8.2-8.282-3.697-3.697-8.212 8.318-8.31-8.203-3.666 3.666 8.321 8.24-8.206 8.313 3.666 3.666 8.237-8.318 8.285 8.203z"></path></svg></span></button> 
            <button  style={{backgroundColor:'green'}} onClick={this.up}  className="buttonDelete"  ><span className="text">Update</span><span style={{borderLeftColor:'green', marginRight:'10%'}} className="icon"><svg width="24" height="24" viewBox="0 0 24 24"><path d="M15.997 13.374l-7.081 7.081L7 18.54l8.997-8.998 9.003 9-1.916 1.916z"/></svg></span></button>   
            </div></div>
            )

            })}
            </ul>
            
        </div>)
    }
}