import React, { Component } from "react";

import {postOneDay} from './apiConnect'
export default class HoursForm extends Component{
state={
  Months:['January','February','March','April','May','June','july','August','September','October','November','December']
}
constructor(props){

  super(props)
  this.Hours=React.createRef();
  this.Setki=React.createRef();
 
  
}
addHours=async()=>{
 
  

 await postOneDay({
                                data:this.props.data,
                                hours:this.Hours.current.value?this.Hours.current.value:0,
                                month:this.state.Months[this.props.month],
                                setki:this.Setki.current.value?this.Hours.current.value:0,
                                isWeek:this.props.isWeek
                              })



}



    render(){
        return(<div>
          <form onSubmit={this.addHours}>
            <div style={{display:'inline-block'}}>
      
          
        <label>
         <p className="formStringFields">Podaj liczbe nadgodzin...</p>
        <input ref={this.Hours} className="formButtons"  max={8} type={"number"}/>
        </label>
             
        
        <label >
         <p className="formStringFields">Podaj liczbe setek...</p>
        <input ref={this.Setki} className="formButtons"  max={8} type={"number"}/>
        </label>
        <br/>
        <input className="submitButton" type="submit" value="Dodaj nadgodziny" />
</div>
      </form>
        <p>{this.props.data}</p>

        </div>)

    }
}