import { Component } from "react";
import React from 'react'




const pictures=[]
function importAll(r) {
    let images = {};
    r.keys().map((item, index) => { images[item] = r(item); return null});
   
    return images;
  }
  
class Koreanki extends Component{
  state={
    pics:[],
    cipka:''

  }

  get=async()=>{
    const cipka = await importAll(require.context('../pics', false, /\.(png|jpe?g|svg)$/));
    for(var key in cipka) {
        var value = cipka[key];
     pictures.push(value)
      
      }
      this.setState({pics:pictures})
    



  }
  constructor(props){
    super(props);
    console.log(props)
    

  }
 
 async componentDidMount(){
    
   await this.get()
  console.log('mount')
   
  }


render(props){

    return (<div>
 <div id='galeria'  >
{   this.state.pics.map((data)=>{ return (
         <div key={data+Math.random()} className='imageContainer'><a href={data} ><img id='pics'  width={300} key={data} alt={data} src={data} /></a>
          
       
         </div>
        
         )
        })}


</div>
    </div>)
}



}
export default Koreanki;