import React  from "react";
import { useState } from 'react';
import { updateHours } from "../apiConnect";
export default function Welcome(props) {
  const [hours, setHours] = useState(0);
  const [nextHours, setNextHours] = useState(0);
  const [setki, setSetki]=useState(0);
  const val = props.val.data;
   
    if(props.up===0){
      
                return  <div > <li alt={{val}} className='HoursList2'><p className='formStringFields'>Data: {props.val.data} </p> <p className='formStringFields'>Godziny: {props.val.hours} </p><p className='formStringFields'>Nadgodziny: {props.val.nexthours} </p><p className='formStringFields'>Setki: {props.val.setki} </p> </li>
               
                </div>
  
    }else{
      
      return <div style={{display:'flex'}}>
                <div style={{width:'300px'}}> <li alt={{val}} className='HoursList2'><p className='formStringFields'>Data: {props.val.data} </p> <p className='formStringFields'>Godziny: {props.val.hours} </p><p className='formStringFields'>Nadgodziny: {props.val.nexthours} </p><p className='formStringFields'>Setki: {props.val.setki} </p> </li></div>
                        <div style={{width:'100px', marginTop:'12%'}}>
                          <form onSubmit={()=>{updateHours({
                                                  data:props.val,
                                                  hours:hours,nextHours:nextHours,
                                                  setki:setki,
                                                  month:props.month
                                                  })}} >
                            <ul style={{ marginTop:'45%'}}>
                              <li  className='HoursList'><input max={8} placeholder={props.val.hours} onChange={(e)=>{setHours(e.target.value)}} className='formButtons' type={'number'}></input></li>
                              <li  className='HoursList'><input max={8} placeholder={props.val.nexthours} onChange={(e)=>{setHours(e.target.value)}} className='formButtons' type={'number'}></input></li>
                              <li  className='HoursList'><input max={8}   placeholder={props.val.setki} onChange={(e)=>{setSetki(e.target.value)}} className='formButtons' type={'number'}></input></li>
                            </ul>
                            <input style={{backgroundColor:'green',borderStyle:'none',color:'white', marginTop:'4%', borderRadius:'10%', marginLeft:'10%', fontSize:'20px'}} type="submit" value="Wyślij" />
                          </form>
                  </div>
              </div>
    }
    
  }