

import React, {Component} from 'react';


import { getListPictures,postOnePicture,deletePics } from './apiConnect';
import '../css/galeria.css'

var picturess=[];

class Galeria extends Component{

state={
    
    pictures: [],
    overload: 1,
    
}

constructor(props){
super(props);

this.cycki=React.createRef();
  this.get();
}



delete=(data)=>{

  deletePics(data);
  this.get();
  this.setState({overload:this.state.overload?0:1})
}
componentDidUpdate(){

}



get=async()=> {
 
    const cycki = await getListPictures().then(value=>{return value.data; })
    picturess=[];
    
    if(cycki!=="undefined"){
    cycki.rows.forEach(element => {
      picturess.push(element.url)});}
      this.setState({pictures:picturess})
 }


add=()=>{
        if(document.getElementById('cipka').value!==''){
         
          postOnePicture(this.cycki.current.value);
        }
        this.get();
        this.setState({overload:this.state.overload?0:1})
        this.cycki.current.value='';
       
}
render(){
    return (<div  style={{background:this.state.color}}>
      <input ref={this.cycki} id='cipka' placeholder='Enter url....' onKeyDown={this.add} onMouseEnter={()=>{this.placeholder=""}} type="text"></input>
            
          <div id='galeria' >
         {   this.state.pictures.map((data)=>{ return (
         <div key={data} className='imageContainer'><a href={data} download><img id="galery" width={300} key={data} alt={data} src={data} /></a>
         
         <div>
         <button  className="buttonDelete"  onClick={()=>{this.delete(data)}}><span className="text">Delete</span><span className="icon"><svg  width="24" height="24" viewBox="0 0 24 24"><path d="M24 20.188l-8.315-8.209 8.2-8.282-3.697-3.697-8.212 8.318-8.31-8.203-3.666 3.666 8.321 8.24-8.206 8.313 3.666 3.666 8.237-8.318 8.285 8.203z"></path></svg></span></button>
          </div>
         </div>
        
         )
        })}
      
          </div>
         
          
           
           
    
    </div>
    
    )}
}

export default Galeria;